import React from "react";
import './Card.css';
import { Col } from 'react-bootstrap';


class Card extends React.Component {
    constructor() {
        super();
        this.state = {}
    }

    render() {
        return (
            <div>
                {this.props.newPost.map((el, i) => {
                    return (
                        <Col md={4} key={i} className="card-cont">
                            <a href="#" className="inner">
                                <div className="img-cont">
                                    <img src={"/image/" + el.image + ".png"} className="card-img img img-responsive" />
                                </div>
                                <div className="content-cont">
                                    <h2>{el.name}</h2>
                                    <p className="price">{el.price} $</p>
                                </div>
                            </a>
                        </Col>
                    )
                })}
            </div>
        )
    }
}


export default Card;
