import React from 'react';
import './Filters.css';
import { Row, Col } from 'react-bootstrap';
import axios from 'axios'
import Card from './Card'

class Filters extends React.Component {
	constructor() {
		super();
		this.products = []
		this.state = {
			post: [],
			selected: "",
			male: [],
			female: [],
			legrand: [],
			kross: [],
			explorer: [],
			visitor: [],
			pony: [],
			force: [],
			ebikes: [],
			ideal: [],
		}
	}

	componentDidMount() {
		this.loadUser();
	}

	loadUser = () => {
		axios('https://json-project3.herokuapp.com/products')
			.then(res => {
				this.products = res.data.slice()
				this.setState({
					post: res.data
				})
				this.setState({
					female: this.filterize('FEMALE', 'gender'),
					male: this.filterize('MALE', 'gender'),
					legrand: this.filterize('LE GRAND BIKES', 'brand'),
					kross: this.filterize('KROSS', 'brand'),
					explorer: this.filterize('EXPLORER', 'brand'),
					visitor: this.filterize('VISITOR', 'brand'),
					pony: this.filterize('PONY', 'brand'),
					force: this.filterize('FORCE', 'brand'),
					ebikes: this.filterize('E-BIKES', 'brand'),
					ideal: this.filterize('IDEAL', 'brand')
				})
			})
	}

	filterize = (filterName, type) => {
		filterName = this.products.filter(function (product) {
			return product[type] === filterName;
		});
		return filterName;
	};

	filter = (e) => {
		const myArr = this.products.filter(el => el.gender.toLowerCase() === e.currentTarget.children[0].innerText.toLowerCase() || el.brand.toLowerCase() === e.currentTarget.children[0].innerText.toLowerCase())
		this.setState({ post: myArr })
		console.log(myArr)
	}

	setFilter = (filter) => {
		this.setState({ selected: filter })
	}
	isActive(value) {
		return ((value === this.state.selected) ? 'active' : '');
	}

	render() {
		return (
			<Row>
				<Col md={3}>
					<h3>Filter by:</h3>
					<ul className="sub-menu">
						<li className={this.isActive('')} onClick={(e) => { this.setFilter(''); this.loadUser(e) }} data-type="all">
							<span>Show all</span><span className="badge" id="allBadge">{this.products.length}</span>
						</li>
					</ul>
					<hr />
					<h4>Gender</h4>
					<ul className="sub-menu">
						<li className={this.isActive('Male')}
							onClick={(e) => { this.setFilter('Male'); this.filter(e) }} data-type="gender">
							<span>Male</span><span className="badge" id="maleBadge">{this.state.male.length}</span>
						</li>
						<li className={this.isActive('Female')}
							onClick={(e) => { this.setFilter('Female'); this.filter(e) }} data-type="gender">
							<span>Female</span><span className="badge" id="femaleBadge">{this.state.female.length}</span>
						</li>
					</ul>
					<hr />
					<h4>Brand</h4>
					<ul className="sub-menu">
						<li className={this.isActive('LE GRAND BIKES')}
							onClick={(e) => { this.setFilter('LE GRAND BIKES'); this.filter(e) }} data-type="brand">
							<span>LE GRAND BIKES</span><span className="badge" id="leGrand">{this.state.legrand.length}</span>
						</li>
						<li className={this.isActive('KROSS')}
							onClick={(e) => { this.setFilter('KROSS'); this.filter(e) }} data-type="brand">
							<span>KROSS</span><span className="badge" id="kross">{this.state.kross.length}</span>
						</li>
						<li className={this.isActive('EXPLORER')}
							onClick={(e) => { this.setFilter('EXPLORER'); this.filter(e) }} data-type="brand">
							<span>EXPLORER</span><span className="badge" id="explorer">{this.state.explorer.length}</span>
						</li>
						<li className={this.isActive('VISITOR')}
							onClick={(e) => { this.setFilter('VISITOR'); this.filter(e) }} data-type="brand">
							<span>VISITOR</span><span className="badge" id="visitor">{this.state.visitor.length}</span>
						</li>
						<li className={this.isActive('PONY')}
							onClick={(e) => { this.setFilter('PONY'); this.filter(e) }} data-type="brand">
							<span>PONY</span><span className="badge" id="pony">{this.state.pony.length}</span>
						</li>
						<li className={this.isActive('FORCE')}
							onClick={(e) => { this.setFilter('FORCE'); this.filter(e) }} data-type="brand">
							<span>FORCE</span><span className="badge" id="force">{this.state.force.length}</span>
						</li>
						<li className={this.isActive('E-BIKES')}
							onClick={(e) => { this.setFilter('E-BIKES'); this.filter(e) }} data-type="brand">
							<span>E-BIKES</span><span className="badge" id="eBikes">{this.state.ebikes.length}</span>
						</li>
						<li className={this.isActive('IDEAL')}
							onClick={(e) => { this.setFilter('IDEAL'); this.filter(e) }} data-type="brand">
							<span>IDEAL</span><span className="badge" id="ideal">{this.state.ideal.length}</span>
						</li>
					</ul>
				</Col>
				<Col md={9}>
					<Row className="card">
						<Card newPost={this.state.post} />
					</Row>
				</Col>
			</Row>
		)
	}
}


export default Filters;
